.movie {
    padding: 100px 0 120px;
    // height: 100vh;

    // height: calc( 100vh + var(--opacity-frame,100vh) + var(--scale-frame,200vh) + var(--space-frame,200px) + 100);

    &.-page {
        padding-top: 0;
    }

    &.-bgcover {
        position: relative;
        z-index: 1;

        &::before {
            position: absolute;
            content: "";
            background-color: rgb(93, 160, 151, 0.35);
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            z-index: -1;
        }
    }

    &__video {
        // position: sticky;
        // top: 0;
        // height: 100vh;

        // &-wrapper {
        //     poa
        // }

        width: 100%;

        @include mq("tablet") {
            display: flex;
            flex-wrap: wrap;
            gap: 20px 20px;
        }

        position: relative;
        z-index: 1;
        height: 207px;
        @include mq("phablet") {
            height: 564px;
        }

        &__item {
            position: absolute;
            z-index: 2;
            width: 100%;
            height: 100%;
            // aspect-ratio: 16 / 9;
            // flex: 0 0 calc((100% / 2) - ((10px * 1) / 2));
            // max-width: calc((100% / 2) - ((10px * 1) / 2));

            // @include mq("tablet") {
            //     flex: 0 0 calc((100% / 3) - ((20px * 2) / 3));
            //     max-width: calc((100% / 3) - ((20px * 2) / 3));
            // }

            &:not(:last-of-type) {
                @include mq("tablet", "max") {
                    margin-bottom: 20px;
                }
            }

            iframe,
            video {
                display: block;
                aspect-ratio: 16 / 9;
                width: 100%;
                height: 100%;
                object-fit: cover;
                // pointer-events: none;
                user-select: none;
            }
        }

        &__thumb {
            position: absolute;
            z-index: 3;
            width: 100%;
            height: 100%;
            top: 0;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center;
                left: 0;
                user-select: none;
            }
        }

        &__wrapper {
            width: 100%;
            height: 100%;
            position: relative;

            &::before {
                position: absolute;
                content: "";
                background-color: rgba(0, 0, 0, 0.45);
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: 4;
            }
        }

        &__btn {
            position: absolute;
            content: "";
            background-image: url(../img/icons/video_play.svg);
            background-repeat: no-repeat;
            background-attachment: fixed;
            background-position: center;
            background-size: cover;
            z-index: 4;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            user-select: none;
            width: 63px;
            height: 63px;
            cursor: pointer;

            @include mq("phablet") {
                width: 172px;
                height: 172px;

            }

        }

        &-btn {
            margin-top: 40px;

            .btn-primary {
                display: block;
                margin: 0 auto;
            }
        }


        &-wrapper {
            width: 100%;
        }
    }

    .title__head {
        justify-content: center;
        align-items: flex-end;
        margin-bottom: 25px;

        @include mq("phablet") {
            margin-bottom: 40px;
        }

        .btn-primary {
            display: none;

            @include mq("phablet") {
                display: block;
            }
        }
    }

    .heading__primary {
        z-index: 5;
        text-align: center;
        width: 100%;

        // @include mq("phablet") {
        //     text-align: left;
        // }
    }

}
