.gallery-list {
    &-row {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        margin-bottom: 20px;
        // gap: 40px;
        // padding-bottom: 40px;

        @include mq("phablet") {
            margin: 0 -5px 0;
        }

        @include mq("tablet") {
            margin: 0 -10px 0;
            // border-bottom: 1px solid $color-bdr;
            // margin-bottom: 40px;
            // gap: 20px;

            // &:nth-last-child(2) {
            //     border-bottom: none;
            //     padding-bottom: 0px;
            //     margin-bottom: 0px;
            // }
        }

        &::after {
            @include mq("phablet") {
                content: "";
                position: absolute;
                // background: $color-white;
                background: $color-off-white;
                width: 100%;
                height: 10px;
                left: 0;
                bottom: 40px;
            }
        }

        // &:last-child {
        //     padding-bottom: 0px;
        // }
    }

    &-col {
        width: 100%;
        margin-bottom: 40px;

        @include mq("phablet") {
            flex: 0 0 calc(100% / 2);
            padding: 0 5px 40px;
            border-bottom: solid 1px $color-bdr;
        }

        @include mq("tablet") {
            flex: 0 0 calc(100% / 3);
            padding: 0 10px 40px;
        }

        .owner-gallery__card {
            width: auto;
            display: block;

            &::after {
                content: none;
            }

            &__img {
                margin-bottom: 10px;
            }

            &__txt {
                display: inline-block;
                background-color: unset;
                color: #222222;
                position: relative;
                bottom: unset;
                left: unset;
            }
        }
    }
}
