.product-list {

    // padding-top: 60px;
    // background-color: $color-off-white;
    &__wrapper {
        .section-desc {
            margin-bottom: 40px;
        }
    }

    &__items {
        // padding: 0 16px;
        display: flex;
        flex-wrap: wrap;
        gap: 2px;
        // @include mq("phablet") {
        //     display: flex;
        //     flex-wrap: wrap;
        // }
    }

    .product {
        &__card {

            // &:not(:last-of-type) {
            //     @include mq("phablet", "max") {
            //         margin-bottom: ;
            //     }
            // }

            text-decoration: none;
            background-color: #00c7b1;
            border: solid 12px #00c7b1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            transition: all 0.25s ease;
            position: relative;
            z-index: 1;
            padding: 0;
            width: 100%;

            &:not(:first-of-type) {
                margin-left: 0;
            }

            @include mq("phablet") {
                flex: 0 0 calc((100% / 2) - 2px);
            }

            @include mq("tablet") {
                flex: 0 0 calc((100% / 3) - 2px);
            }


            &:hover {
                .product__img {
                    img {
                        transform: translateY(-10px);
                    }
                }
            }

            &-inner {
                background-color: #ffffff;
                border-radius: 15px;
                padding-top: 15px;
            }
        }

        &__img {
            height: auto;
            border-bottom-left-radius: 15px;
            border-bottom-right-radius: 15px;
            position: relative;

            img {
                aspect-ratio: unset;
                transition: transform 0.3s ease;
            }
        }
    }
}
