.pagetitlebar {
    position: relative;
    isolation: isolate;
    padding: 75px 0;
    @include mq("phablet") {
        padding: 125px 0;
    }
    &__title {
        font-family: $font-bebas;
        color: $color-white;
        font-size: 6rem;
        font-weight: 600;
        line-height: 1.35;
    }

    &__subtitle {
        color: $color-white;
        font-size: 2rem;
        font-weight: 700;
        line-height: 1;
    }

    &__img {
        position: absolute;
        isolation: isolate;
        top: 0;
        left: 0;
        object-fit: cover;
        object-position: center;
        height: 100%;
        width: 100%;
        z-index: -3;

        &::before,
        &::after {
            content: "";
            position: absolute;
            inset: 0;
        }

        &::before {
            background-image: url(../img/pagetitlebar/texture.png);
            mix-blend-mode: overlay;
        }

        &::after {
            background-color: $color-black-3;
            mix-blend-mode: multiply;
            opacity: 20%;
        }

        &.op-30 {
            &::after {
                opacity: 30%;
            }
        }

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: bottom;
        }
        &.-center {
            img {
                object-position: center;
            }
        }
       
    }
    &.-product {
        .pagetitlebar__img:before {
            content: none;
        }
        img {
            object-position: center;
        }
    }
    &.-notexture {
        .pagetitlebar__img:before {
            content: none;
        }
        img {
            object-position: center;
        }
    }
}
