.contact-nav {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    margin-bottom: 54px;

    @include mq("tablet") {
        margin-bottom: 60px;
    }

    &::before {
        content: "";
        display: block;
        background-color: $color-primary;
        width: 100%;
        height: 2px;
        position: absolute;
        top: 50%;
        left: 0;
        z-index: -1;
        transform: translateY(-50%);
    }

    &__state {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-white;
        width: 100px;
        height: 40px;
        border: 2px solid $color-primary;
        border-radius: 4px;

        color: $color-primary;
        font-size: 1.4rem;
        font-weight: 700;

        transition: 0.25s ease;
        transition-property: background-color, color;

        @include mq("phablet") {
            width: 160px;
            height: 40px;
        }

        @include mq("tablet") {
            font-size: 1.6rem;
            width: 200px;
            height: 40px;
        }

        @include mq("bootstrap-lg") {
            width: 250px;
        }

        &.-active {
            background-color: $color-primary;
            color: $color-white;
        }

        // &:hover {
        //     background-color: $color-primary;
        //     color: $color-white;
        // }
    }


}