html {
    font-size: 10px;
    width: 100%;
    height: 100vh;
    height: 100dvh;
    overflow: hidden;
}

body {
    font-family: $font-primary;
    font-size: 1.6rem;
    font-weight: 400;
    color: $color-black;
    line-height: 1.6;
    background: $color-white;
    // background-image: url(../img/parallax-img.jpg);
    // background-repeat: no-repeat;
    // background-size: cover;
    // background-attachment: fixed;
    // background-position: center 79px;
    // margin-top: 107px;
    width: 100%;
    height: 100vh;
    height: 100dvh;
    overflow: auto;

    @include mq("bootstrap-lg") {
        // background-position: center 82px;
    }

    &.hide-ovrflw {
        overflow: hidden;
    }

    &.-no-bg {
        background-image: none;
    }
}

.container {
    display: block;
    max-width: 100%;
    padding: 0 15px;
    margin: 0 auto;

    @include mq("tablet") {
        max-width: 970px;
    }
}

%reset-ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

* {
    box-sizing: border-box;
}

.-bgcover {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.section {
    background-color: $color-white;
    padding-bottom: 120px;
}

.fixed-img {
    img {
        display: block;
        width: 100%;
        height: 350px;
        object-fit: cover;
    }

    &.-about {
        img {
            object-position: center 70%;
        }
    }
}

.main {
    margin-top: -79px;
    position: relative;
    z-index: 1;

    @include mq("tablet") {
        margin-top: -107px;
    }

    &-wrapper {
        margin-top: -79px;
        position: relative;
        z-index: 1;

        @include mq("tablet") {
            margin-top: -107px;
        }
    }
}

.main-white {
    background: $color-white;
}

.main-bg {
    background-color: $color-off-white;

    .section,
    .breadcrumb {
        background-color: $color-off-white;
    }

    section.brand-story {
        background-color: $color-gray-2;
    }

    section.product-owner-gallery {
        background-color: $color-lgt-gray;
    }

    section.brand-concept {
        @include mq("tablet", "max") {
            background-color: $color-black-3;
        }
    }
}

.swiper-no-swiping .swiper-wrapper {
    @include mq("tablet") {
        transform: none !important;
    }
}

.load-screen {
    position: fixed;
    inset: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: red;
    z-index: 1111;
    display: none;
}

.scroll {
    &-parallax {
        position: relative;
        z-index: 0;

        &.-top {
            height: calc(var(--parallax-height) * 3.9 + 100rem);
            margin-top: calc(var(--parallax-height) * -4);

            @include mq("tablet") {
                height: calc(var(--parallax-height) * 4.3 + 100rem);
                margin-top: calc(var(--parallax-height) * -4);
            }
        }

        &.-bottom {
            height: calc(var(--parallax-height) * 2);
            margin-bottom: calc(var(--parallax-height) * -1);
            height: calc(var(--parallax-height) * 2 + 100rem);
        }

        &-body {
            position: sticky;
            top: 0;
        }
    }
}

.gallary {
    &-scroll {
        display: flex;
        position: relative;

        &-slider {
        }

        &-card {
        }
    }
}

.scale {
    &-wrapper {
        position: sticky;
        top: 0;
    }
}

.footer-parallax {
    position: relative;
    z-index: 0;

    &-body {
        position: sticky;
        top: 0;
    }
}

.fade-up {
    opacity: 0;
    transform: translateY(50px);
    transition: 1.5s ease;
    transition-property: opacity, transform;

    &.animate {
        opacity: 1;
        transform: translateY(0);
    }
}

.sticky-fade-up {
    opacity: 0;
    transform: translateY(50px);
    transition: 1.5s ease;
    transition-property: opacity, transform;

    &.animate {
        opacity: 1;
        transform: translateY(0);
    }
}

.banner {
    @include mq("tablet", "max") {
        padding-bottom: 60px;
    }

    a {
        display: block;
    }
    &.-single {
        padding-bottom: 0;
        padding-top: 60px;
        @include mq("tablet") {
            padding-top: 120px;
        }
    }
}
main{
    &.page-store{
        margin-top: 0 !important;
    }
}