.about-brand {
    padding: 0 0 120px;

    @include mq("tablet") {
        padding: 0 0 120px;
    }

    &__wrapper {
        .heading__primary {
            margin-bottom: 35px;
        }

        .section-desc {
            margin-bottom: 40px;
        }
    }

    &__banner {
        img {
            display: block;
            // height: 200px;
            height: auto;
            width: 100%;
            // object-fit: cover;
            // object-position: bottom;
            // @include mq("phablet"){
            //     height: 450px;
            // }
        }
    }
}

.brand-concept {
    position: relative;

    @include mq("tablet", "max") {
        background-color: $color-black-3;
    }
    .heading__primary {
        margin-bottom: 32px;
        @include mq("tablet") {
            margin-bottom: 28px;
        }
    }

    &__vid {
        position: relative;

        @include mq("tablet", "max") {
            margin-bottom: 80px;
        }

        &::after {
            @include mq("tablet") {
                content: "";
                position: absolute;
                inset: 0;
                background-color: $color-black-3;
                opacity: 0.6;
                mix-blend-mode: multiply;
            }
        }

        video {
            display: block;
            width: 100%;
            object-fit: cover;
            height: 225px;
            @include mq("phablet"){
                height: 500px;
            }
            @include mq("tablet") {
                height: 792px;
            }
        }
    }

    &__desc {
        @include mq("tablet") {
            position: absolute;
            top: 45%;
            left: 0;
            transform: translateY(-50%);
            width: 100%;
        }
    }
}

.brand-products {
    &__subttl {
        margin-bottom: 40px;
    }

    &__banner {
        margin-bottom: 60px;
    }
}

.brand-safty {
    padding: 120px 0;
    background-color: $color-white;
    position: relative;
    isolation: isolate;

    &::before {
        content: "";
        position: absolute;
        inset: 0;
        background-color: $color-lgt-gray;
        opacity: 0.4;
        z-index: -1;
    }

    &__ttl {
        margin-bottom: 60px;
    }
}

.brand-element {
    padding: 120px 0;

    &__wrapper {
    }

    &__banner {
        margin-bottom: 40px;

        img {
            display: block;
            object-fit: cover;
            @include mq("phablet"){
                height: 350px;
            }
        }
    }

    &__subttl {
        margin-bottom: 40px;
    }
}

.brand-story {
    padding: 120px 0;

    &__wrapper {
        & > .section-desc {
            margin-bottom: 60px;
        }
    }

    &__ttl {
        margin-bottom: 60px;
    }
}

.brand-details {
    &.-count {
        .brand-details {
            &__img {
                position: relative;

                @include mq("tablet", "max") {
                    padding-left: 20px;
                }

                span {
                    font-family: $font-bebas-expanded;
                    color: $color-primary;
                    font-size: 5rem;
                    font-weight: 700;
                    font-style: italic;
                    line-height: 1;
                    position: absolute;
                    top: -14px;
                    left: 0;

                    @include mq("tablet") {
                        left: -20px;
                    }
                }
            }

            &__row {
                &:not(:last-of-type) {
                    margin-bottom: 60px;
                }
            }
        }
    }

    &__row {
        @include mq("tablet") {
            display: flex;
            gap: 30px;
        }

        &:not(:last-of-type) {
            margin-bottom: 40px;
        }
    }

    &__img {
        flex-shrink: 0;

        @include mq("tablet", "max") {
            margin-bottom: 10px;
        }

        img {
            display: block;

            @include mq("tablet", "max") {
                width: 100%;
            }
        }
    }
}

.brand-compliance {
    .compliance {
        &__row {
            display: flex;
            flex-direction: row;
            gap: 30px;
            margin: 0 auto 40px;

            &:last-child {
                margin-bottom: 0px;
            }
        }

        &__col {
        }

        &__desc {
        }

        &__ttl {
            font-size: 2.2rem;
            line-height: calc(31 / 22);
            font-weight: 500;
            margin-bottom: 20px;
            isolation: isolate;
            position: relative;

            &::after {
                content: "";
                background: #5da097;
                z-index: -1;
                opacity: 0.4;
                position: absolute;
                width: 20px;
                height: 20px;
                top: 13px;
                left: -4px;
            }
        }

        &__para {
            font-size: 1.6rem;
            line-height: calc(26 / 16);
        }
    }
}

.brand-craft {
    padding: 120px 0;

    &__wrapper {
        .section-desc {
            margin-bottom: 40px;
        }
    }
}

.brand-feature {
    &__wrapper {
        .heading__primary {
            margin-bottom: 35px;
        }

        .section-desc {
            margin-bottom: 40px;
        }
    }

    &__row {
        @include mq("tablet") {
            display: flex;
            flex-wrap: wrap;
            row-gap: 34px;
            column-gap: 20px;
        }

        &.-d-sm-flex {
            @include mq("tablet", "max") {
                display: flex;
                flex-wrap: wrap;
                gap: 20px 9px;
            }

            .brand-feature__col {
                @include mq("tablet", "max") {
                    flex: 0 0 calc(50% - (9px / 2));
                    margin-bottom: 0;
                }
            }
        }
    }

    &__col {
        @include mq("tablet") {
            flex: 0 0 calc(100% / 3 - 40px / 3);
        }

        &:not(:last-of-type) {
            @include mq("tablet", "max") {
                margin-bottom: 30px;
            }
        }
    }

    &__img {
        margin-bottom: 10px;

        img {
            display: block;
            width: 100%;
            aspect-ratio: 169 / 101;
            object-fit: cover;
            @include mq("phablet"){
                aspect-ratio: 300 / 180;
            }
        }
    }

