.cta {
    padding-bottom: 0;

    &.-sticky {
        position: sticky;
        top: 0;
        z-index: 1;
    }

    &__wpr {
        display: flex;
        flex-direction: column;

        @include mq("phablet") {
            flex-direction: row;
        }
    }

    &__inner {
        display: block;
        position: relative;
        z-index: 1;
        transition: all 0.25s ease;
        @include mq("phablet"){
            width: 50%;
        }
        &::after {
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: 0;
            transition: all 0.25s ease;
            background: rgba(0, 0, 0, 0.65);

        }

        &:hover {
            &::after {
                background: rgba(93, 160, 151, 0.65);
            }

            img {
                transform: scale(1.1);
            }
        }

        &.-ovlay-green {
            &::after {
                background-color: rgba(93, 160, 151, 0.7);
            }

            &:hover {
                &::after {
                    background: rgba(0, 0, 0, 0.7);
                }
            }
        }

        // &:first-child {
        //     &::after {
        //         background: rgba(0, 0, 0, 0.65);
        //     }

        //     &:hover {
        //         &::after {
        //             background: rgba(93, 160, 151, 0.65);
        //         }
        //     }
        // }

        // &:last-child {
        //     &::after {
        //         background-color: rgba(93, 160, 151, 0.7);
        //     }

        //     &:hover {
        //         &::after {
        //             background: rgba(0, 0, 0, 0.7);
        //         }
        //     }
        // }
    }

    &__img {
        overflow: hidden;

        img {
            display: block;
            width: 100%;
            // height: 320px;
            height: 150px;
            object-fit: cover;
            transition: all 0.25s ease;

            // @include mq("tablet") {
            //     height: unset;
            // }
            @include mq("desktop") {
                // aspect-ratio: 660 / 320;
                height: unset;
                aspect-ratio: 537 / 150;
            }
        }
    }

    &__txt {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        font-size: 6rem;
        font-family: $font-condensed;
        font-weight: 500;
        color: $color-white;
        line-height: 1.4;
        text-align: center;
        width: 100%;
        span {
            font-size: 1.4rem;
            font-weight: 500;
            display: block;
            font-family: $font-primary;
        }
    }
}
