.release {
    .news {
        padding-bottom: 80px;
        padding-top: 70px;
        @include mq("tablet") {
            padding-bottom: 120px;
            padding-top: 110px;
        }
        .heading__primary {
            margin-bottom: 40px;
        }
        .tabs {
            margin-bottom: unset;
        }
        &__lnk {
            align-items: center;
            justify-content: center;
            padding: 23px 0;
            width: 100%;

            @include mq("tablet") {
                padding: 44px 0;
            }

            &:hover {
                background-color: unset;
            }
        }

        &__content {
            width: 100%;

            @include mq("tablet") {
                width: calc(100% - 150px);
            }
            @include mq("bootstrap-lg") {
                width: calc(100% - 250px);
            }
            @include mq("desktop") {
                width: calc(100% - 350px);
            }
        }

        &__date {
            width: 100%;

            @include mq("tablet") {
                gap: 80px;
            }
            span {
                &:last-child {
                    font-weight: 400;
                }
            }
        }
    }
    .brand-concept {
        @include mq("tablet", "max") {
            padding-bottom: 60px;
        }
        .heading__primary {
            font-size: 1.8rem;
            font-family: $font-condensed;
            font-weight: 600;
            font-style: italic;
            line-height: 1;
            margin-bottom: 0;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            @include mq("phablet") {
                font-size: 2.5rem;
            }
            @include mq("tablet") {
                font-size: 3rem;
                // line-height: 1.2;
            }
            @include mq("bootstrap-lg") {
                font-size: 3.5rem;
            }
            @include mq("desktop") {
                font-size: 4rem;
            }
            span {
                font-size: 4.8rem;
                font-family: $font-condensed;
                font-style: italic;
                margin-top: 0;
                letter-spacing: 0;
                @include mq("phablet") {
                    font-size: 5.3rem;
                }
                @include mq("tablet") {
                    font-size: 6.3rem;
                    margin-top: 5px;
                }
                @include mq("bootstrap-lg") {
                    font-size: 7.8rem;
                }
                @include mq("desktop") {
                    font-size: 10.7rem;
                }
            }
        }
        &__vid {
            @include mq("tablet", "max") {
                // margin-bottom: 68px;
                margin-bottom: 0;
                display: none;
            }
        }
        &__desc{
            @include mq("tablet","max"){
                padding-top: 60px;
            }
        }
    }
}
.release {
    &.footer {
        background-color: rgba(0, 0, 0, 1);
    }
    & .footer {
        &-mid {
            padding: 40px 0;
            @include mq("tablet"){
                padding: 30px 0;
            }
            &__wrapper {
                display: flex;
                gap: 50px;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                gap: 25px;

                @include mq("tablet") {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    align-items: center;
                    gap: 50px;
                }
            }

            &__link {
                justify-content: center;
                gap: 10px;
                &:not(:last-of-type) {
                    margin-bottom: 15px;

                    @include mq("tablet") {
                        margin-bottom: unset;
                    }
                }
            }
        }
        &-bottom {
            padding: 20px 0;
        }
    }
}

.media {
    background-color: #fff;
    padding: 20px 0;
    @include mq("tablet") {
        border-top: solid 1px #c6c6c6;
    }
    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 30px;
    }

    &-icon {
        transition: all 0.23s ease;
        &:hover {
            opacity: 0.7;
        }

        img {
            display: block;
        }
    }
}
