.timer {
    video {
        display: block;
        width: 100%;
        height: 100vh;
        height: 100dvh;
        object-fit: cover;
        object-position: center;
        @include mq("phablet","max"){
            height: 350px;
        }
        // @include mq("phablet") {
        //     height: calc(100vh - 298px);
        // }
        // @include mq("tablet") {
        //     height: calc(100vh - 208px);
        // }
        // @include mq("desktop") {
        //     height: calc(100vh - 255px);
        // }
    }
    &__wpr {
        background-color: rgba(14, 14, 14, 1);
        padding: 32px 0 35px;
        @include mq("tablet") {
            padding: 25px 0 35px;
        }
    }
    &__ttl {
        font-size: 2.5rem;
        font-weight: 700;
        color: $color-white;
        border-bottom: solid 1px $color-white;
        margin-bottom: 20px;
        text-align: center;
        line-height: 1.4;
        padding-bottom: 8px;
        @include mq("phablet") {
            font-size: 2.65rem;
        }
        @include mq("tablet") {
            font-size: 2.8rem;
            border-bottom: solid 2px $color-white;
            line-height: 1;
            padding-bottom: 18px;
            margin-bottom: 10px;
        }
        @include mq("bootstrap-lg") {
            font-size: 3.1rem;
        }
        @include mq("desktop") {
            font-size: 3.7rem;
        }
    }
    &__inner {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        @include mq("phablet") {
            justify-content: center;
        }
        @include mq("tablet") {
            flex-wrap: nowrap;
        }
        @include mq("desktop") {
            justify-content: flex-start;
        }
    }
    &__note {
        font-size: 1.4rem;
        font-weight: 700;
        color: $color-white;
        margin-bottom: 8px;
        margin-right: 20px;
        @include mq("phablet") {
            font-size: 1.5rem;
        }
        @include mq("tablet") {
            font-size: 1.55rem;
            border: solid 2px $color-white;
            padding: 11px 8px;
            margin-bottom: 0;
        }
        @include mq("bootstrap-lg") {
            font-size: 1.9rem;
        }
        @include mq("desktop") {
            font-size: 2.4rem;
        }
    }
    &__dates {
        display: flex;
        justify-content: center;
        @include mq("tablet", "max") {
            width: 100%;
        }
    }
    &__txt {
        font-size: 3.4rem;
        font-weight: 700;
        font-family: "DSEG7 Classic";
        letter-spacing: -0.1em;
        color: $color-white;
        @include mq("phablet") {
            font-size: 6.05rem;
        }
        // @include mq("tablet") {
        //     font-size: 6.05rem;
        // }
        @include mq("bootstrap-lg") {
            font-size: 5.7rem;
        }
        @include mq("desktop") {
            font-size: 8rem;
        }
        span {
            font-size: 1.2rem;
            font-weight: 600;
            font-family: $font-bebas;
            letter-spacing: 0;
            text-transform: capitalize;
            margin-left: 8px;
            @include mq("phablet") {
                font-size: 1.6rem;
            }
            @include mq("tablet") {
                font-size: 1.8rem;
                margin-left: 15px;
            }
            @include mq("bootstrap-lg") {
                font-size: 2rem;
            }
            @include mq("desktop") {
                font-size: 2.6rem;
            }
        }
        &:not(:last-child) {
            margin-right: 14px;
            @include mq("tablet") {
                margin-right: 30px;
            }
        }
    }
}
