.insta {
    padding-top: 80px;
    padding-bottom: 80px;
    background-color: $color-primary;
    overflow: hidden;
    position: relative;
    z-index: 1;

    &::after {
        position: absolute;
        content: "";
        background-image: url(../img/insta/insta-overlay.png);
        background-repeat: no-repeat;
        background-size: cover;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        mix-blend-mode: overlay;
        z-index: -1;
    }

    &-bg {
        background-image: url(../img/insta/insta-palm-tree.png);
        background-position: center;
        background-size: cover;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: -2;
        transform: translateZ(0);
    }

    .heading__primary span {
        margin-top: 27px;
    }

    .title__head {
        margin-bottom: 30px;
        justify-content: center;
        @include mq("tablet", "max") {
            display: block;
        }

        .heading__primary {
            text-align: center;
        }
    }

    &__img {
        // margin-bottom: 40px;
        picture {
            display: block;
        }

        img {
            display: block;
            object-fit: cover;
            aspect-ratio: 345 / 716;

            @include mq("tablet") {
                aspect-ratio: 940 / 460;
            }
        }
    }

    &__btn {
        text-align: center;
        margin-top: 40px;
    }
    #sb_instagram #sbi_images {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        height: 100%;
        @include mq("tablet") {
            grid-template-rows: 1fr 1fr;
            grid-template-columns: 1fr 1fr 1fr 1fr;
        }
    }
    #sb_instagram .sbi_item {
        &:first-child {
            grid-row-start: 1;
            grid-column-start: 1;
            grid-row-end: 3;
            grid-column-end: 3;
            @include mq("tablet") {
                grid-row-start: 1;
                grid-column-start: 1;
                grid-row-end: 3;
                grid-column-end: 3;
            }
            .sbi_photo {
                height: 345px !important;
                @include mq("phablet") {
                    height: 420px !important;
                }
                @include mq("tablet") {
                    height: 380px !important;
                }
                @include mq("bootstrap-lg") {
                    height: 460px !important;
                }
            }
        }
    }
    #sb_instagram.sbi_col_3 #sbi_images .sbi_item {
        width: auto;
    }
    #sb_instagram .sbi_photo_wrap {
        height: 100%;
    }
    #sb_instagram .sbi_photo {
        height: 165px !important;
        @include mq("phablet") {
            height: 200px !important;
        }
        @include mq("tablet") {
            height: 180px !important;
        }
        @include mq("bootstrap-lg") {
            height: 220px !important;
        }
    }
    #sb_instagram.sbi_width_resp {
        margin-bottom: 40px;
        @include mq("tablet") {
            margin-bottom: 0;
        }
    }
}
