        &-prev {
            &::after {
                background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' standalone='no'%3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 20010904//EN' 'http://www.w3.org/TR/2001/REC-SVG-20010904/DTD/svg10.dtd'%3E%3Csvg version='1.0' xmlns='http://www.w3.org/2000/svg' width='114.000000pt' height='18.000000pt' viewBox='0 0 114.000000 18.000000' preserveAspectRatio='xMidYMid meet'%3E%3Cg transform='translate(0.000000,18.000000) scale(0.100000,-0.100000)' fill='%23FFFFFF' stroke='none'%3E%3Cpath d='M102 120 c-91 -68 -96 -73 -87 -100 6 -20 16 -20 566 -20 l559 0 0%0A35 0 35 -478 0 c-263 0 -482 3 -485 6 -3 4 5 15 18 26 14 11 25 27 25 37 0 17%0A-18 41 -31 41 -4 0 -43 -27 -87 -60z'/%3E%3C/g%3E%3C/svg%3E%0A");
            }
        }

        &-next {
            &::after {
                background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' standalone='no'%3F%3E%3C!DOCTYPE svg PUBLIC '-//W3C//DTD SVG 20010904//EN' 'http://www.w3.org/TR/2001/REC-SVG-20010904/DTD/svg10.dtd'%3E%3Csvg version='1.0' xmlns='http://www.w3.org/2000/svg' width='114.000000pt' height='20.000000pt' viewBox='0 0 114.000000 20.000000' preserveAspectRatio='xMidYMid meet'%3E%3Cg transform='translate(0.000000,20.000000) scale(0.100000,-0.100000)' fill='%23FFFFFF' stroke='none'%3E%3Cpath d='M927 173 c-14 -13 -6 -49 13 -61 11 -7 20 -17 20 -22 0 -6 -173 -11%0A-477 -12 l-478 -3 -3 -32 -3 -31 543 -4 c299 -2 551 -1 560 0 21 5 31 29 20%0A48 -5 8 -41 39 -82 69 -69 52 -98 64 -113 48z'/%3E%3C/g%3E%3C/svg%3E%0A");
            }
