        &-pagination {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            gap: 10px;
            width: auto !important;
            z-index: 99;
            // @include mq("tablet") {
            //     display: none;
            // }

            .swiper-pagination-bullet {
                background-color: $color-lgt-gray;
                margin: 0 !important;
                opacity: 1;
                width: 12px;
                height: 12px;
                &.swiper-pagination-bullet-active {
                    background-color: $color-primary;
                }
            }
        }
    }

    &__slider {
        width: 345px;
        max-width: calc(100% - 20px);
        position: absolute;
        top: 45%;
        left: 50%;
        transform: translate(-50%, -50%);
        aspect-ratio: 300 / 286;

        @include mq("tablet") {
            aspect-ratio: 300 / 250;
            width: 350px;
        }

        @include mq("desktop") {
            width: 450px;
        }

        &-outer {
            position: relative;
        }

        &-wrapper {
            width: 100%;
            height: 100%;
        }

        // animatino
        &-sticky {
            width: 100%;
            height: calc(100vh - 62px);
            height: calc(100dvh - 62px);
            left: 0;
            overflow: hidden;
            position: sticky;
            top: 0;
            isolation: isolate;

            @include mq("tablet") {
                height: 100vh;
                height: 100dvh;
            }

            // &::before {
            //     content: "";
            //     display: block;
            //     background-color: $color-black-5;
            //     width: 100%;
            //     height: calc(100% - 87px - 40px);
            //     position: absolute;
            //     bottom: 0;
            //     left: 0;
            //     z-index: -1;
            // }

            // &::before,
            // &::after {
            //     content: "";
            //     position: absolute;
            //     inset: 0;
            //     background-repeat: no-repeat;
            //     background-position: top center;
            //     background-size: cover;
            // }

            // &::before {
            //     background-image: url(../img/gallery/gallery-bg.png);
            //     mix-blend-mode: screen;
            //     z-index: -1;
            // }

            // &::after {
            //     background-image: url(../img/gallery/gallery-bg02.png);
            //     z-index: -2;
            // }

            // &::before {
            //     content: "";
            //     display: block;
            //     position: absolute;
            //     top: 0;
            //     left: 0;
            //     width: 100%;
            //     height: 100%;
            // }
        }

        &-inner {
            width: 100%;
            height: 100vh;
            height: 100dvh;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
        }

        &-wrapper {
            display: flex;
            flex-wrap: nowrap;
            transform-origin: center 1250%;
            transition: transform 0.3s linear;

            @include mq("tablet") {
                transform-origin: center 1450%;
            }
        }
    }
    // .overlay-slide .owner-gallery__card::after {
