    &__card {
        text-decoration: none;
        display: block;
        height: 100%;
        flex-shrink: 0;
        // width: 393px;
        // width: 100%;
        // width: 345px;
        width: 100%;
        display: flex;
        flex-direction: column;

        overflow: hidden;
        position: absolute;
        left: 0;
        top: 0;

        // rotate
        // transform-origin: center 1250%;

        @include mq("tablet") {
            // width: 300px;
            // aspect-ratio: 300 / 250;
            // transform-origin: center 1450%;
        }

        @include mq("desktop") {
            // width: 450px;
        }

        // &::after {
        //     content: "";
        //     background-color: rgba(18, 18, 18, 0.9);
        //     position: absolute;
        //     inset: 0;
        //     z-index: 2;
        //     opacity: 0;
        //     transition: opacity 0.3s ease;
