        //             opacity: 1;
        //         }
        //     }
        // }

        &:nth-of-type(1) {
            position: relative;
        }

        &:hover {
            .owner-gallery__card__img {
                &::after {
                    visibility: visible;
                    opacity: 0.5;
                }

                .read-more {
                    visibility: visible;
                    opacity: 1;
                }
            }

            .owner-gallery__card__txt {
                &::after {
                    right: 10px;
                }
            }
        }

        &__img {
            position: relative;
            // margin-bottom: 10px;
            flex-grow: 1;

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                opacity: 0;
                visibility: hidden;
                background-color: rgb(0, 0, 0);
                z-index: 1;
                transition: all 0.3s ease;
            }

            .read-more {
                color: $color-white;
                font-size: 1.8rem;
                font-weight: 700;
                white-space: nowrap;
                position: absolute;
                top: 38%;
                left: 50%;
                transform: translate(-50%, 50%);
                opacity: 0;
                visibility: hidden;
                z-index: 2;
                transition: all 0.3s ease;
            }
        }

        figure {
            position: relative;
            height: 100%;

            img {
                display: block;
                // aspect-ratio: 300 / 286;
                width: 100%;
                height: 100%;
                object-fit: cover;

                // @include mq("tablet") {
                //     aspect-ratio: 300 / 250;
                // }
            }

            .caption {
                background-color: $color-white;
                font-family: $font-bebas;
                color: $color-black;
                text-align: center;
                font-size: 1.6rem;
                font-weight: 600;
                position: absolute;
                top: 0;
                left: 0;
                width: 114px;
                height: 39px;
                border-bottom-right-radius: 4px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        &__txt {
            // color: $color-black;
            color: $color-primary;
            text-decoration: none;
            font-size: 1.8rem;
            font-weight: 700;
            // display: flex;
            // align-items: center;
            padding-right: 47px;
            display: inline-block;
            // position: relative;
            position: absolute;
            bottom: 0;
            left: 0;
            z-index: 1;
            background-color: rgba(0, 0, 0, 0.5);
            padding-left: 10px;

            span {
                font-size: 1.4rem;
                font-weight: 400;
                padding-left: 2px;
            }

            &::after {
                position: absolute;
                content: "";
                display: block;
                width: 16px;
                height: 10px;
                margin-left: 20px;
                background-image: url(../img/icons/arrow-right-green.svg);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                transition: all 0.25s ease;
                top: 10px;
                right: 15px;
            }
        }
    }

    &__btn {
        text-align: center;
        // position: absolute;
        // left: 50%;
        // bottom: 30px;
        // transform: translateX(-50%);
        z-index: 1;

        &.fade-up {
            transform: translateY(50px);

            &.animate {
                transform: translateY(0);
            }
        }
    }
}
