.breadcrumb {
    background-color: $color-white;
    padding: 13px 0 60px;
    @include mq("tablet"){
        padding: 20px 0 60px;
    }

    &__wpr{
        overflow-x: auto;
        overflow-y: hidden;
        text-align: center;

        @include mq("phablet", "max") {
            overflow-x: scroll;
        }
    }

    &__list {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        white-space: nowrap;
        // overflow-x: auto;
        // overflow-y: hidden;
        // flex-wrap: nowrap;
    }

    &__item {
        font-size: 1.6rem;
        font-weight: 500;
        color: $color-black-2;
        position: relative;
        padding: 0 20px;

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;
        }

        &:not(:last-child) {
            &::after {
                position: absolute;
                content: ">";
                top: 50%;
                transform: translateY(-50%);
                right: -4px;
                font-size: 1.6rem;
                font-weight: 500;
                color: $color-black-2;
            }

            // @include mq("tablet"){
            //     padding-left: 25px;
            // }
        }
    }

    &__link {
        text-decoration: none;
        color: $color-black-2;
        transition: opacity 0.25s ease;
        &.active {
            pointer-events: none;
        }
        @include mq("tablet") {
            &:hover {
                opacity: 0.7;
            }
        }
    }
}
