.pagination {
    // background-color: $color-off-white;
    // padding-top: 60px;
    list-style-type: none;
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 30px;
    }
    li {
        list-style-type: none;
    }
    &__item {
        // margin-bottom: 15px;
        a {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 2.4rem;
            font-family: $font-bebas-expanded;
            font-weight: 600;
            font-style: italic;
            line-height: 1;
            color: $color-bdr;
            text-decoration: none;
            transition: all 0.25s ease;
            position: relative;
            padding-bottom: 10px;
            &:hover {
                color: $color-primary;
                &::after{
                    opacity: 1;
                }
            }
            &::after {
                position: absolute;
                content: "";
                background-color: $color-primary;
                width: 19px;
                height: 1px;
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
                transition: all 0.25s ease;
                opacity: 0;
            }
        }

        &.-dots {
            a {
                &::after{
                    content: none;
                }
            }
        }

        &.-active {
            a {
                color: $color-primary;
                &::after{
                    opacity: 1;
                }
            }
        }

        &.-prev,
        &.-next {
            a {
                border-bottom: none;
                &:hover {
                    // svg{
                    //     fill:$color-primary;
                    // }
                    path,
                    line {
                        stroke: $color-primary;
                    }
                }
                &::after{
                    content: none;
                }
            }
        }

        // &.-next,
        // &.-last,
        // &.-first,
        // &.-prev{
        //     display: none;

        //     @include mq("tablet"){
        //         display: inline-block;
        //     }
        // }
    }
}
