    .filter-list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px 5px;
        margin-bottom: 40px;

        @include mq("tablet") {
            gap: 10px;
        }

        &-item {
            border-radius: 4px;
            border: 2px solid $color-primary;
            padding: 3px 20px;
            font-size: 1.6rem;
            line-height: calc(21/16);
            font-weight: 700;
            color: $color-primary;
            transition: all 0.25s ease-in;
            font-family: $font-bebas;

            &:hover {
                background: $color-primary;
                color: $color-white;
            }

            &.active {
                background: $color-primary;
                color: $color-white;
            }
        }
    }