.news {
    padding-top: 0;
    background-color: $color-off-white;
    position: relative;
    z-index: 1;

    &__wrapper {}

    &-fixed {
        width: 100%;
        height: 100vh;
        height: 100dvh;
        display: flex;
        align-items: center;
        justify-content: center;
        position: sticky;
        top: 0;
        z-index: -1;

        &-img {
            width: 100%;
            height: 100vh;
            height: 100dvh;
            position: relative;
            transition: height 0.2s linear;

            picture,
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                background-color: rgba(255, 255, 255, 0.6);
                z-index: 1;
                opacity: 0;
                transition: opacity 0.6s ease 0.2s;
            }

            &.-active {
                &::after {
                    opacity: 1;
                }
            }
        }
    }

    .tabs {
        margin-bottom: 40px;

        @include mq("tablet") {
            margin-bottom: 60px;
        }

        &__nav {
            border-bottom: solid 1px $color-bdr;
            margin-bottom: 10px;
            padding-left: 15px;
            padding-right: 15px;

            @include mq("tablet") {
                padding-left: 0;
                padding-right: 0;
            }

            ul {
                display: flex;
                justify-content: center;
            }
        }

        &__lnk {
            font-size: 1.4rem;
            font-weight: 500;
            color: $color-black-2;
            padding: 0 12px 10px 12px;
            text-decoration: none;
            display: inline-block;
            transition: all 0.25s ease;

            @include mq("tablet") {
                padding: 0 30px 10px 30px;
            }

            &.active {
                border-bottom: solid 2px $color-primary;
            }

        }
    }

    &__lnk {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 0;
        border-bottom: solid 1px $color-bdr;
        text-decoration: none;
        color: $color-black-2;
        transition: all 0.25s ease;

        &:hover {
            background-color: #e9e9e9;
        }
    }

    &__content {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        width: calc(100% - 140px);

        @include mq("tablet") {
            flex-direction: row;
            gap: 60px;
            width: calc(100% - 228px);
            align-items: center;
        }
    }

    &__date {
        width: 213px;
        max-width: 100%;
        font-size: 1.4rem;
        font-weight: 500;
        display: flex;
        gap: 20px;
        margin-bottom: 10px;

        @include mq("tablet") {
            gap: 28px;
            margin-bottom: 0;
        }
    }

    &__desc {
        font-size: 1.4rem;
        font-weight: 400;
        @include line-clamp(3);

        @include mq("tablet") {
            width: calc(100% - 273px);
            @include line-clamp(1);
        }
    }

    &__img {
        img {
            display: block;
            aspect-ratio: 120 / 72;
            object-fit: cover;
        }
    }

    &-body {
        position: relative;
        z-index: 1;

        &__wrapper {}

        &__btn {
            text-align: center;
        }
    }

    &-content {
        margin-bottom: 40px;

        &__row {
            @include mq("tablet") {
                display: flex;
                flex-wrap: wrap;
                gap: 30px 20px;
            }

            @include mq("bootstrap-lg") {
                gap: 30px 40px;
            }
        }

        &__col {
            @include mq("tablet") {
                flex: 0 0 calc((100% / 3) - ((20px * 2) / 3));
                max-width: calc((100% / 3) - ((20px * 2) / 3));
            }

            @include mq("bootstrap-lg") {
                flex: 0 0 calc((100% / 3) - ((40px * 2) / 3));
                max-width: calc((100% / 3) - ((40px * 2) / 3));
            }

            &:not(:last-of-type) {
                margin-bottom: 30px;

                @include mq("tablet") {
                    margin-bottom: 0;
                }
            }
        }

        &-card {
            display: block;
            color: $color-black-3;

            @include mq("tablet") {
                background-color: $color-off-white;
                padding-bottom: 10px;
            }

            &:hover {
                .news-content-card__img {
                    img {
                        transform: scale(1.1);
                    }
                }
            }

            &__img {
                margin-bottom: 14px;
                overflow: hidden;

                img {
                    aspect-ratio: 120 / 72;
                    width: 100%;
                    object-fit: cover;
                    transform-origin: center;
                    transition: transform 0.5s ease;
                }
            }

            &__desc {
                @include mq("tablet") {
                    padding: 0 10px;
                }

                &-text {
                    font-size: 1.4rem;
                    font-weight: 400;
                }
            }

            &__info {
                font-size: 1.4rem;
                font-weight: 500;
                margin-bottom: 8px;
            }

            &__date {
                margin-right: 16px;
            }

            &__tag {}
        }
    }
}
