.parallax-section {
    background: #000;
    height: 350px;
    // height: 700px;
    overflow: hidden;
    position: relative;
    &.-mt-60{
        margin-top: 60px;
    }
    .parallax-content {
        align-items: center;
        color: #fff;
        display: flex;
        font-size: 2rem;
        letter-spacing: 0.1em;
        justify-content: center;
        height: 100%;
        position: relative;
        z-index: 2;
    }
    .parallax-image {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        height: 800px;
        left: 0;
        position: absolute;
        top: 0;
        transform: translateZ(0);
        width: 100%;
        z-index: 1;
    }
}
