.product {
    &-web {
    }
    &-van {
        &__banner {
            padding: 20px 10px;
            border-top: 1px solid $color-black;
            border-bottom: 1px solid $color-black;
            max-width: 720px;
            margin: 0 auto 40px;
            @include mq("tablet", "max") {
                padding: 10px;
            }
        }
        &__ttl {
            font-size: 3.8rem;
            font-weight: 700;
            line-height: calc(38 / 25.6);
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
            flex-wrap: wrap;
            @include mq("tablet", "max") {
                justify-content: center;
                font-size: 2.8rem;
                line-height: calc(39 / 28);
                text-align: center;
            }
            &-wrapper {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                gap: 10px;
            }
            &-jp {
                font-size: 0.9rem;
                letter-spacing: -0.04em;
                font-weight: 700;
                line-height: calc(12 / 11);
                text-align: center;
                padding: 4px;
                background-color: $color-orange-2;
                color: $color-white;
                border-radius: 4px;
                width: 100%;
                max-width: 60px;
                height: 60px;
                flex-shrink: 0;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
            }
            &-num {
                display: block;
                font-size: 3.2rem;
                font-weight: 700;
                line-height: calc(32 / 29);
                color: $color-yellow;
            }
        }
    }
}

.van-web {
    .bodycolor {
        margin-bottom: 40px;
        padding-top: 0;

        .wp-content {
            h3 {
                line-height: 1.375;
            }
        }
    }
}
.van {
    &-img {
        img {
            @include mq("tablet", "max") {
                aspect-ratio: 345/140;
            }
        }
    }
    &__slider {
        user-select: none;
        pointer-events: none;
        img {
            display: block;
            width: 100%;
            aspect-ratio: 230 / 150;
            object-fit: cover;
            @include mq("phablet"){
                aspect-ratio: 285 / 190;
            }
        }
        &.swiper-free-mode > .swiper-wrapper {
            transition-timing-function: linear;
        }
    }

    &__img {
        img {
            width: 100%;
            display: block;
        }
    }

    .swiper .swiper-wrapper {
        @include mq("tablet") {
            margin-left: -40px;
        }
    }
}
.product-van {
    padding-bottom: 120px;
    .product-equip__content {
        background-color: $color-black-3;
        padding: 30px;
        color: $color-white;
        @include mq("tablet", "max") {
            padding: 20px;
        }
    }
    .product-equip__caption {
        background-color: unset;
        padding-inline: unset;
        height: unset;
        margin-bottom: 10px;
        @include mq("tablet", "max") {
            font-size: 1.5rem;
        }
    }
    .product-equip__desc {
        min-height: 126px;
        font-size: 1.6rem;
        line-height: calc(25 / 16);
        font-weight: 500;
        @include mq("tablet", "max") {
            font-size: 1.4rem;
            line-height: calc(25 / 14);
            min-height: 150px;
        }
    }
    .product-equip__block {
        &::after {
            height: 438px;
        }
    }
}
.product-kitchen__wrapper {
    .section-desc {
        margin-bottom: 40px;
    }
}
.kitchen {
    &-galary {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
        @include mq("tablet", "max") {
            gap: 10px;
        }
        &__fig {
            width: calc(100% / 3 - (20px * 2) / 3);
            @include mq("tablet", "max") {
                flex: 0 0 calc(100% / 2 - (10px) / 2);
                width: calc(100% / 2 - (10px) / 2);
            }
        }
        &__inner {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 20px;
            @include mq("tablet", "max") {
                gap: 10px;
                flex-wrap: wrap;
                justify-content: flex-start;
            }
        }
        &__caption {
            font-size: 1.8rem;
            font-weight: 700;
            text-align: center;
            padding: 4px 0 5px;
            background-color: $color-primary;
            color: $color-white;
            @include mq("tablet", "max") {
                font-size: 1.4rem;
            }
        }
        &__top {
            display: flex;
            gap: 30px;
            @include mq("tablet", "max") {
                flex-wrap: wrap;
            }
        }
    }
}
.van {
    padding: 120px 0 108px;
    position: relative;
    .product-price-table {
        th {
            // &:first-of-type {
            //     width: 50%;
            // }
            &:last-of-type {
                width: 50%;
            }
        }
        tr {
            &:nth-of-type(2) {
                td {
                    &:nth-of-type(1),
                    &:nth-of-type(2) {
                        width: 24.47%;
                    }
                }
            }
        }
    }
    .van-product__btn {
        position: absolute;
        bottom: -33px;
        left: 50%;
        transform: translate(-50%);
    }
    .product-program__wrapper {
        color: $color-white !important;
    }
    .product-program-catalog__row {
        justify-content: center;
    }
    .product-price-table__top {
        justify-content: flex-end;
    }
    &.product-price {
        margin-bottom: 33px;
    }
}

.product-retreat {
    &__ul {
        max-width: 750px;
    }
    .kitchen-galary__inner {
        gap: 10px;
    }
    .kitchen-galary__caption {
        font-size: 1.4rem;
        font-weight: 400;
        line-height: calc(19 / 14);
        background-color: unset;
        color: $color-black;
        text-align: start;
        margin-top: 5px;
        @include mq("tablet","max"){
            margin-top: unset;
        }
    }
    .kitchen-galary__inner {
        align-items: flex-start;
    }
}
.kitchen-option {
    padding: 20px;
    background-color: $color-lgt-gray;
    margin-top: 40px;
    @include mq("tablet", "max") {
        padding: 15px;
        margin-top: 20px;
    }
    .heading__tertiary {
        text-align: start;
        &::after {
            left: 2.9%;
            @include mq("tablet", "max") {
                left: 8.3%;
            }
        }
    }
    &__items {
        padding-bottom: 30px;
        margin-bottom: 10px;
        border-bottom: 1px solid $color-black;
        display: flex;
        justify-content: space-between;
        gap: 20px;
        @include mq("tablet", "max") {
            flex-direction: column;
            align-items: center;
            gap: 37px;
            padding-bottom: 25px;
        }
    }
    &__item {
        display: flex;
        gap: 20px;
        flex: 0 0 calc(100% / 2 - (30px) / 2);
        width: calc(100% / 2 - (30px) / 2);
        align-items: flex-end;

        @include mq("tablet", "max") {
            width: 100%;
            flex: 0 0 100%;
            gap: 10px;
        }

        img {
            @include mq("bootstrap-lg", "max") {
                max-width: 152px;
            }
            @include mq("tablet", "max") {
                max-width: 100%;
            }
            @include mq("phablet", "max") {
                max-width: 152px;
            }
        }

        &-ttl {
            font-size: 1.4rem;
            font-weight: 400;
            line-height: calc(19 / 14);
            margin-bottom: 5px;
        }
        &-rate {
            font-size: 1.6rem;
            font-weight: 700;
            line-height: calc(19 / 16);
        }
    }
    &__charge {
        padding: 20px 0;
        border-bottom: 1px solid $color-gray-3;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &:last-of-type {
            margin-bottom: 15px;
        }
        @include mq("tablet", "max") {
            flex-direction: column;
            align-items: flex-start;
        }

        &-name {
            font-size: 1.6rem;
            font-weight: 700;
            line-height: calc(25 / 16);
            letter-spacing: 0.01em;
            color: $color-primary;
        }
        &-amount {
            font-size: 1.8rem;
            font-weight: 700;
            line-height: calc(19 / 18);
            span {
                font-size: 1.6rem;
                font-weight: 400;
                margin-right: 10px;
            }
        }
    }
    &__note {
        font-size: 1.4rem;
        line-height: calc(19 / 14);
        font-weight: 400;
        &:last-of-type{
            margin-top: 5px;
            @include mq("tablet","max"){
                margin-top: 10px;
            }
        }
    }
}
