.page-faq {
    background-color: $color-off-white;
    padding-bottom: 100px;
    .breadcrumb {
        background-color: $color-off-white;
    }
    .wp-content h2 {
        text-align: center;
    }
    &__cats {
        display: flex;
        flex-wrap: wrap;
        gap: 0 50px;
        margin-bottom: 120px;
        @include mq("tablet") {
            gap: 0 30px;
        }
        @include mq("bootstrap-lg") {
            gap: 0 50px;
        }
        li {
            @include mq("phablet") {
                width: calc(50% - 25px);
            }
            @include mq("bootstrap-lg") {
                width: 280px;
            }
        }
        .contact__link {
            @include mq("phablet") {
                width: 100%;
            }
            @include mq("bootstrap-lg") {
                width: 280px;
            }
        }
    }
    &__inner {
        &:not(:last-child) {
            margin-bottom: 80px;
        }
    }
    .store-acc {
        margin-bottom: 20px;
    }
    .tabs {
        &__nav {
            margin-bottom: 60px;
            ul {
                display: flex;
                justify-content: center;

                li {
                    width: 33.33%;
                }
            }
        }
        &__lnk {
            font-size: 2rem;
            font-family: $font-bebas;
            font-weight: 600;
            padding: 20px 10px;
            text-align: center;
            text-transform: capitalize;
            color: $color-bdr;
            display: block;
            line-height: 1.2;
            transition: all 0.25s ease;
            border-bottom: solid 1px $color-bdr;
            @include mq("phablet") {
                font-size: 2.2rem;
            }
            @include mq("tablet") {
                font-size: 2.4rem;
                padding: 15px;
            }
            @include mq("bootstrap-lg") {
                font-size: 2.6rem;
            }
            @include mq("desktop") {
                font-size: 2.8rem;
            }
            &.active {
                background-color: $color-primary;
                color: $color-white;
                border-bottom: solid 1px $color-primary;
                &:hover {
                    background-color: $color-primary;
                    color: $color-white;
                    border-bottom: solid 1px $color-primary;
                }
            }
            &:hover {
                background-color: $color-bdr;
                color: $color-black;
                // border-bottom: solid 1px $color-bdr;
            }
        }
    }

