.page-title {
    background-color: $color-black-3;
    padding: 50px 0 75px;

    @include mq("tablet") {
        padding: 60px 0 40px 85px;
    }

    .heading {
        position: relative;

        &--bar {
            position: relative;
            display: flex;
            align-items: center;
            color: $color-white;
            @include mq("tablet") {
                display: inline-block;
            }

            span {
                font-size: 16px;
                color: $color-white;
                text-align: center;
                display: block;
                padding-left: 20px;
    
                @include mq("tablet") {
                    padding: 0;
                }
            }
            em {
                font-size: 16px;
                color: $color-gray;
                display: inline-block;
                padding-top: 5px;

                @include mq("phablet") {
                    padding-bottom: 10px;
                    padding-left: 20px;
                }
            }
        }

        &-primary {
            font-size: 32px;
            font-weight: 700;
            font-family: $font-sec;
            padding-right: 20px;
            @include mq("tablet") {
                font-size: 40px;
                padding-right: 0;
            }
        }

        &-note {
            font-size: 16px;
            color: $color-gray;
            display: inline-block;
            padding-bottom: 10px;
            @include mq("phablet", "max") {
                padding-left: 8px;
                padding-top: 15px;
            }
        }
    }
}
