            }
        }

        &__bot {
            max-height: 0;
            // padding-inline: 10px;
            overflow: hidden;
            transition: max-height 0.3s ease;

            @include mq("tablet") {
                padding-inline: 10px;
            }
        }

        &__desc {
            padding-block: 20px;
            text-align: center;
            font-size: 1.6rem;
        }
    }

    &-catalog {
        border: 2px solid $color-primary;

        &__row {
            @include mq("tablet") {
                display: flex;
            }
        }

        &__card {
            // border: 2px solid $color-primary;
            padding: 40px 10px;

            &:not(:last-child) {
                border-bottom: 2px solid $color-primary;

                @include mq("tablet") {
                    border-right: 2px solid $color-primary;
                    border-bottom: navajowhite;
                }
            }

            @include mq("tablet") {
                flex: 0 0 calc(100% / 2);
                max-width: calc(100% / 2);
            }

            // &:first-of-type {
            //     border-bottom-width: 1px;

            //     @include mq("tablet") {
            //         border-right-width: 1px;
            //     }
            // }

            // &:last-of-type {
            //     border-top-width: 1px;

            //     @include mq("tablet") {
            //         border-left-width: 1px;
            //     }
            // }
        }

        &__card2 {
            // border: 2px solid $color-primary;
            padding: 40px 10px;

            &:not(:last-child) {
                border-bottom: 2px solid $color-primary;

                @include mq("tablet") {
                    border-right: 2px solid $color-primary;
                    border-bottom: navajowhite;
                }
            }

            @include mq("tablet") {
                flex: 0 0 calc(100% / 3);
                max-width: calc(100% / 3);
            }

            // &:first-of-type {
            //     border-bottom-width: 1px;

            //     @include mq("tablet") {
            //         border-right-width: 1px;
            //     }
            // }

            // &:last-of-type {
            //     border-top-width: 1px;

