.product-owner-gallery {
    background-color: $color-off-white;
    padding-top: 120px;
    overflow: hidden;

    &__wrapper {
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        margin-bottom: 60px;

        .owner-gallery__card {
            flex: 0 0 calc((100% / 3) - ((20px * 2) / 3));
        }
    }

    &__btn {
        text-align: center;
    }
}

.bodycolor {
    background-color: $color-off-white;
    padding-top: 120px;

    .tabs {
        margin-bottom: 40px;

        &__nav {
            &-wpr {
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                max-width: 592px;
                margin: 0 auto;
                gap: 20px;

                @include mq("tablet") {
                    justify-content: center;
                    gap: 10px 20px;
                }
            }

            ul {
                display: flex;
                flex-wrap: wrap;
                gap: 20px;

                @include mq("tablet") {
                    gap: 10px 20px;
                }

                // &:not(:last-child) {
                //     margin-bottom: 20px;
                //     @include mq("tablet") {
                //         margin-bottom: 0;
                //     }
                // }

                li {
                    width: 48px;
                    height: 48px;

                    a {
                        width: 48px;
                        height: 48px;
                        box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
                        display: block;
                        transition: all 0.25s ease;

                        &.active {
                            box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.3);
                            transform: translateY(-5px);
                        }

                        &:hover {
                            box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.3);
                            transform: translateY(-5px);
                        }
                    }
                }
            }
        }
    }

    &__img {
        max-width: 720px;
        margin: 0 auto;
    }

    .wp-content {
        h3 {
            text-align: center;

            display: flex;
            justify-content: center;
            align-items: flex-end;

            @include mq("[phablet", ",max") {
                min-height: 112px;
            }

            @include mq("tablet") {
                line-height: 1;
            }
        }
    }

    .section-desc {
        text-align: center;
    }

    // .bodycolor__img{
    //     animation: fadeEffect 4s;
    //     animation-delay: 2ms;
    // }
}

@keyframes fadeEffect {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.product-program-acc__bot {
    .top-faq {
        padding: 20px 0;

        // background-color: $color-white;
        // padding-top: 80px;
        // padding-bottom: 80px;
        .faq__wpr {
            margin-bottom: 0;
        }

        .faq__heading,
        .faq__answer {
            span {
                font-family: $font-primary;
            }
        }

        .faq__heading {
            background-image: url(../../../img/teaser/topfaq-bg.jpg);
            border-radius: 0;
            font-size: 1.8rem;
            font-weight: 700;
            text-align: center;
            justify-content: center;
            line-height: 1.5;
            padding: 20px 48px 20px 10px;
            flex-direction: column;

            @include mq("phablet") {
                font-size: 2rem;
            }

            @include mq("tablet") {
                font-size: 2.2rem;
                padding: 20px 68px 20px 30px;
            }

            @include mq("bootstrap-lg") {
                font-size: 2.4rem;
            }

            @include mq("desktop") {
                font-size: 2.6rem;
            }

            &::after {
                font-size: 3.4rem;
            }

            span {
                display: block;
                font-size: 1.6rem;
                line-height: 1.4;
                padding-bottom: 0;

                @include mq("tablet") {
                    font-size: 1.8rem;
                    line-height: 1.2;
                }

                @include mq("bootstrap-lg") {
                    font-size: 2.4rem;
                }

                @include mq("desktop") {
                    font-size: 2.6rem;
                }

                // &:last-child {
                //     font-size: 1.8rem;
                //     // font-weight: 500;
                //     @include mq("phablet") {
                //         font-size: 2rem;
                //     }
                //     @include mq("tablet") {
                //         font-size: 2.2rem;
                //     }
                //     @include mq("bootstrap-lg") {
                //         font-size: 2.4rem;
                //     }
                //     @include mq("desktop") {
                //         font-size: 2.6rem;
                //     }
                // }
            }
        }

        .faq__answer {
            display: block;
            padding: 0;
            // max-height: 100%;
            // display: none;
            // transition: none;
        }

        .table-ttl {
            font-size: 1.8rem;
            font-weight: 500;
            max-width: 180px;
            width: 100%;
            color: $color-white;
            padding: 10px 15px;
            line-height: 1;
            margin-bottom: 10px;

            @include mq("phablet") {
                font-size: 2rem;
            }

            @include mq("tablet") {
                font-size: 2.2rem;
                max-width: 200px;
            }

            &.-blue {
                background-color: #5b9bd5;
            }

            &.-green {
                background-color: #70ad47;
            }
        }

        &__table {
            overflow-x: scroll;
            position: relative;

            @include mq("tablet") {
                overflow-x: inherit;
            }

            &::-webkit-scrollbar {
                -webkit-appearance: none;
            }

            &::-webkit-scrollbar:vertical {
                width: 5px;
            }

            &::-webkit-scrollbar:horizontal {
                height: 5px;
            }

            &::-webkit-scrollbar-track {
                background-color: #e2e2e2;
                width: 5px;
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 8px;
                background-color: $color-gray;
                width: 15px;
            }

            table {
                width: 740px;
                margin-bottom: 15px;

                // @include mq("tablet") {
                //     width: 950px;
                // }
                @include mq("tablet") {
                    width: 100%;
                    margin-bottom: 0;
                }

                tr {
                    border: solid 2px $color-black;
                }

                td {
                    font-size: 1.7rem;
                    // font-weight: 400;
                    font-weight: 400;
                    color: $color-black;
                    padding: 10px;
                    text-align: center;
                    vertical-align: middle;

                    @include mq("phablet") {
                        font-size: 1.9rem;
                    }

                    @include mq("bootstrap-lg") {
                        font-size: 2.4rem;
                    }

                    &:not(:last-child) {
                        border-right: solid 1px $color-black;
                    }

                    &.bold {
                        font-weight: 700;
                    }
                }

                .gray {
                    background-color: #f9fafb;
                    font-weight: 700;
                }

                .title {
                    // font-size: 1.7rem;
                    font-weight: 700;
                    // background-color: #d9e9f6;
                    background-color: #bddcd4;
                    text-align: center;

                    // &:first-child{
                    // }
                    &:last-child {
                        background-color: #d8e1e0;
                    }

                    &.-green {
                        background-color: #dfecd5;
                        width: 180px;
                        vertical-align: middle;
                    }
                }

                .subtitle {
                    // background-color: #e2e2e2;
                    font-weight: 700;
                    width: 130px;
                    // font-size: 1.7rem;

                    &.green {
                        background-color: #bddcd4;
                    }

                    &.blue {
                        background-color: #d8e1e0;
                    }
                }
            }
        }

        .table-wpr {
            margin-bottom: 60px;

            &:last-child {
                margin-bottom: unset;
            }
        }

        .note {
            font-size: 1.2rem;
            font-weight: 500;
            color: $color-black;
            // text-align: right;
            display: flex;
            justify-content: flex-end;
            padding: 10px 0;
            line-height: 1.4;

            &.txt-lft {
                text-align: left;

                @include mq("tablet") {
                    text-align: right;
                }
            }

            &.-abs {
                position: absolute;
                right: 0;
                top: -20px;
                padding: 0;
            }
        }

        span {
            background-color: transparent;
            height: auto;
            min-width: unset;
            margin-right: 0;
            color: $color-black;
            border-radius: 0;
        }

        .product-program-catalog__link span {
            color: $color-primary;
        }
        .product-program-catalog__link:hover span {
            color: $color-white;
        }
    }

    .flow {
        &__description {
            margin-bottom: 10px;
        }

        &__ttl {
            font-size: 2rem;
            font-weight: 700;
            color: #000;
            padding: 5px 10px;
            background-color: #ffffff;
            margin-bottom: 30px;
        }

        &__img {
            text-align: center;
            margin-bottom: 30px;
            display: block;
        }

        &__desc {
            font-size: 1.4rem;
            font-weight: 400;
            color: #000;
            line-height: 1.6;
            padding-bottom: 30px;

            @include mq("tablet") {
                font-size: 1.6rem;
            }
        }

        &__table {
            overflow-x: scroll;

            @include mq("bootstrap-lg") {
                overflow-x: hidden;
            }

            &::-webkit-scrollbar {
                -webkit-appearance: none;
            }

            &::-webkit-scrollbar:vertical {
                width: 5px;
            }

            &::-webkit-scrollbar:horizontal {
                height: 5px;
            }

            &::-webkit-scrollbar-track {
                background-color: #e2e2e2;
                width: 5px;
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 8px;
                background-color: $color-gray;
                width: 15px;
            }
        }

        table {
            border: solid 2px $color-black;
            width: 870px;
            margin-bottom: 15px;

            @include mq("tablet") {
                width: 890px;
            }

            @include mq("bootstrap-lg") {
                width: 100%;
                margin-bottom: 0;
            }

            td {
                border-bottom: solid 2px $color-black;
                border-right: solid 2px $color-black;
                padding: 5px;
                font-size: 1.6rem;
                vertical-align: middle;
                line-height: 1.5;

                &.gray {
                    background-color: #e2e2e2;
                    text-align: center;
                    font-weight: 700;

                    &:first-child {
                        width: 250px;
                    }

                    &:last-child {
                        width: 300px;
                    }
                }

                &.sky {
                    background-color: #d9e9f6;
                    font-weight: 500;

                    span {
                        font-size: 1.8rem;
                        text-align: left;
                        justify-content: flex-start;
                        font-weight: 700;
                    }
                }

                &.num {
                    text-align: center;
                    font-weight: 500;
                    font-size: 1.2rem;

                    width: 230px;

                    span {
                        font-weight: 400;
                        // font-size: 2rem;
                        padding-bottom: 5px;
                        display: block;
                        font-size: 1.7rem;

                        @include mq("phablet") {
                            font-size: 1.9rem;
                        }

                        @include mq("bootstrap-lg") {
                            font-size: 2.4rem;
                        }
                    }
                }

                &:last-child {
                    font-weight: 500;
                }
            }
        }
    }

    .purchase {
        &__img {
            text-align: center;
            margin-bottom: 40px;
        }
    }
}
