.heading {
    &__primary {
        font-size: 6rem;
        line-height: 1;
        margin-bottom: 30px;
        font-family: $font-condensed;
        font-weight: 500;

        @include mq("tablet") {
            margin-bottom: 40px;
        }

        span {
            display: block;
            font-size: 1.4rem;
            font-weight: 500;
            font-family: $font-primary;
            margin-top: 13px;

            &.lg {
                font-weight: 700;
                font-size: 2.8rem;
                line-height: 1.4;

                @include mq("phablet") {
                    font-size: 2.9rem;
                }

                @include mq("bootstrap-lg") {
                    font-size: 3rem;
                }
            }
            &.mt{
                margin-top: 23px;
            }
        }

        &.-center {
            text-align: center;
        }

        &.-white {
            color: $color-white;
        }

        // &::selection {
        //     color: red;
        // }

    }

    &__secondary {
        font-family: $font-condensed;
        font-size: 2.8rem;
        font-weight: 500;
        line-height: 1.4;
        padding-bottom: 11px;
        border-bottom: 1px solid $color-bdr;
        position: relative;
        margin-bottom: 25px;

        @include mq("tablet") {
            font-size: 3.2rem;
        }

        &::after {
            content: "";
            display: block;
            background-color: $color-primary;
            width: 50px;
            height: 2px;
            position: absolute;
            bottom: -2px;
            left: 0;
        }

        span {
            display: block;
            font-size: 1.2rem;
        }

        &.-center {
            text-align: center;

            &::after {
                left: 50%;
                transform: translateX(-50%);
            }
        }

        &.-no-line {
            border-bottom: none;
            padding: 0;
            margin-bottom: 0;

            &::after {
                content: none;
            }
        }
    }

    &__tertiary {
        color: $color-black-3;
        text-align: center;
        font-size: 2.4rem;
        font-weight: 500;
        line-height: 1.4;
        padding-bottom: 10px;
        border-bottom: 1px solid $color-bdr;
        position: relative;
        margin-bottom: 25px;

        &::after {
            content: "";
            display: block;
            background-color: $color-primary;
            width: 50px;
            height: 2px;
            position: absolute;
            bottom: -1px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &__forth {
        color: $color-black-3;
        font-size: 2rem;
        font-weight: 500;
        line-height: 1.4;
        padding-left: 5px;
        padding-bottom: 3px;
        position: relative;
        isolation: isolate;
        margin-bottom: 20px;

        @include mq("tablet") {
            font-size: 2.2rem;
        }

        &::before {
            content: "";
            display: block;
            background-color: $color-primary;
            width: 20px;
            height: 20px;
            opacity: 0.5;
            position: absolute;
            top: 15px;
            left: 0;
            z-index: -1;
        }

        &.-center {
            width: fit-content;
            margin: 0 auto;
            margin-bottom: 20px;
        }
    }
}

.section-desc {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.6;

    &.-center {
        text-align: center;
    }

    &.-fs-lg {

        @include mq("tablet") {
            font-size: 1.8rem;
        }
    }

    &.-white {
        color: $color-white;
    }

    span {
        text-transform: uppercase;
    }
}

.title__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;

    .heading__primary {
        margin-bottom: 0;
    }
}

.page-ttl,
.section-ttl {
    color: $color-black-2;
    text-align: center;
    font-size: 2.6rem;
    font-weight: 700;
    line-height: 1.4;
    margin-bottom: 35px;

    @include mq("tablet") {
        font-size: 2.8rem;
    }

    &.-start {
        text-align: start;
    }
}
