.movie {
    &__row {
        display: flex;
        gap: 43px;
        flex-wrap: wrap;

        @include mq("tablet") {
            gap: 30px;
        }

        &.-mb83 {
            margin-bottom: 83px;
        }
    }

    &__col {
        @include mq("tablet") {
            flex: 0 0 calc(100% / 2 - 30px / 2);
        }

        &.hide {
            display: none;
        }
    }

    &__img {
        img {
            width: 100%;
            display: block;
        }
    }

    &__ttl {
        font-size: 1.8rem;
        font-family: $font-bebas;
        line-height: calc(24 / 18);
        font-weight: 700;
        color: $color-primary;
        margin-block: 9px 4px;
    }

    .page-ttl {
        &.hide {
            display: none;
        }
    }

    &__text {
        font-size: 2.2rem;
        font-weight: 500;
        line-height: calc(31 / 22);
        color: $color-black-3;
        margin-bottom: 20px;
        padding-left: 5px;
        z-index: 1;
        position: relative;

        @include mq("tablet") {
            margin-bottom: 22px;
        }

        &::after {
            content: "";
            background-color: $color-primary;
            width: 20px;
            height: 20px;
            top: 10px;
            left: 0px;
            position: absolute;
            z-index: -1;
            opacity: 0.4;
        }
    }

    &__para {
        font-size: 1.6rem;
        line-height: calc(26 / 16);
        color: $color-black-2;
    }

    &__video {
        &.-page {
            height: 195px;

            @include mq("phablet") {
                height: 255px;
            }
        }

        .movie__video__btn {
            width: 95px;
            height: 95px;
        }
       
    }
    &.-page{
        .movie__col {
            @include mq("tablet","max"){
                width: 100%;
            }
        }
    }
}
