.about {
    padding-bottom: 60px;

    @include mq("tablet") {}

    .heading__primary {
        font-size: 3.5rem;
        margin-bottom: 30px;
        line-height: 1.4;

        @include mq("tablet") {
            font-size: 3.8rem;
        }

        @include mq("tablet") {
            font-size: 4rem;
        }

        @include mq("desktop") {
            font-size: 4.5rem;
        }

        span.lg {
            font-size: 3rem;

            @include mq("tablet") {
                font-size: 4.2rem;
            }

            @include mq("desktop") {
                font-size: 6rem;
            }
        }

        img {
            max-width: 75%;
        }
    }

    &-content {
        &__img {
            margin-bottom: 40px;

            img {
                @include mq("bootstrap-lg") {
                    width: 70%;
                }
            }
        }
    }

    &__contents {
        padding-top: 30px;
        padding-bottom: 30px;

        @include mq("phablet") {
            padding-top: 115px;
            padding-bottom: 60px;
            display: flex;
            align-items: center;
        }

        @include mq("tablet") {
            min-height: 100vh;
            min-height: 100dvh;
            padding-bottom: 120px;
        }
    }

    .section-desc {
        @include mq("tablet") {
            font-size: 1.8rem;
        }

        @include mq("bootstrap-lg") {
            font-size: 2.2rem;
        }
    }

    &__row {
        text-align: center;
    }

    &__col {
        &:first-child {
            width: 100%;
            flex-shrink: 0;
        }

        &:last-child {
            width: 100%;

            @include mq("tablet") {
                margin-bottom: 0;

            }
        }
    }

    .section-desc {
        margin-bottom: 40px;

        @include mq("tablet", "max") {
            font-size: 1.4rem;
        }
    }

    &__slider {
        user-select: none;
        margin: 0 0 60px;
        pointer-events: none;

        img {
            display: block;
            width: 100%;
        }

        &.swiper-free-mode>.swiper-wrapper {
            transition-timing-function: linear;
        }
    }

    &__img {
        img {
            width: 100%;
            display: block;
        }
    }

    .swiper .swiper-wrapper {
        @include mq("tablet") {
            margin-left: -40px;
        }
    }
}
