    &__confirmation {
        font-size: 1.4rem;
        line-height: 2;
        text-align: center;
        margin: 48px 0 80px;
        font-weight: 400;
    }

    &__btn {
        display: flex;
        justify-content: center;
        margin-top: 60px;
        gap: 20px;

        @include mq("tablet") {
