    span.mwform-checkbox-field.horizontal-item {
        margin-left: 0;
    }

    .custom-radio {
        .form-control:checked + .mwform-radio-field-text::before {
            background-image: url(.././img/icon-form.svg);
        }

        .mwform-radio-field-text {
            margin-right: 40px;
            position: relative;
            display: inline-flex;
            height: 26px;
            align-items: center;
            padding-left: 41px;
            font-weight: 400;
            cursor: pointer;

            &::before {
                content: "";
                // background-image: url(.././img/icon-form.svg);
                background-image: url(.././img/icon-form-1.svg);
                background-position: center;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                width: 26px;
                height: 26px;
                position: absolute;
                left: 0;
                top: 0;
            }
        }
    }

    // extra code for wordpress
    .mw_wp_form_confirm {
        .contact-form__field {
            border: 1px solid #e5e5e5;
            padding: 10px;
            background-color: #e5e5e5;
            height: 40px;
            display: flex;
            align-items: center;
            &.-textarea {
                min-height: 250px;
                align-items: flex-start;
            }
        }
        .contact-form__field-select {
            height: auto;
            // width: 267px;
            &::after {
                content: none;
            }
        }
