    }

    .mwform-checkbox-field {
        display: flex;
        align-items: center;
        width: 50%;

        @include mq("phablet") {
            width: auto;
        }

        &:not(:last-child) {
            margin-right: 0;
            margin-bottom: 18px;

            @include mq("phablet") {
                margin-right: 41px;
            }
        }

        &-text {
            line-height: 1.5;
            position: relative;
            padding-left: 28px;
            font-weight: 400;
            cursor: pointer;

            @include mq("tablet") {
                padding-left: 41px;
            }

            &::before {
                content: "";
                background-image: url(.././img/icon-rec.svg);
                background-position: center;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                width: 20px;
                height: 20px;
                position: absolute;
                left: 0;
                top: 1px;

                @include mq("tablet") {
                    width: 26px;
                    height: 26px;
                    top: 0;
                }
            }
        }

        label {
            display: flex;
            align-items: center;
        }

        input {
            position: absolute;
            left: 0;
            z-index: -1;
            width: 1rem;
            height: 1.25rem;
            opacity: 0;
        }
    }
}

