.store {
    background-color: $color-white;
    &.-bg-white{
        background-color: $color-white;
    }
    &__row {    
        display: flex;
        flex-direction: column;
        // flex-wrap: wrap;
        row-gap: 60px;

        @include mq("tablet") {
            flex-direction: row;
            gap: 80px 20px;
            flex-wrap: wrap;
        }
    }

    &__col {
        &:hover {
            transition: all 0.25s ease-in;
        }

        @include mq("tablet") {
            flex: 0 0 calc(100% / 3 - (40px / 3));
        }
    }
    &__card{
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-between;
    }
    &__img {
        overflow: hidden;

        img {
            width: 100%;
            transition: all 0.25s ease-in;
            display: block;
            aspect-ratio: 345 / 207;
            object-fit: cover;
            @include mq("tablet"){
                aspect-ratio: 300 / 180;
            }
        }
    }

    &__card {
        &:hover {
            .store__img {
                img {
                    transform: scale(1.08, 1.08);
                    opacity: 0.7;
                }
            }
        }
    }

