    &__desc {
        margin-bottom: 31px;

        a {
            font-size: 1.6rem;
            font-weight: 700;
            line-height: calc(28 / 16);
            color: $color-black-3;
            text-decoration: underline;
            transition: all 0.25s ease-in;

            &:hover {
                opacity: 0.7;
            }
        }

        img {
            margin-right: 10px;
            margin-top: 4px;
        }

        .-txt {
            font-size: 1.4rem;
            line-height: calc(20 / 14);
            font-weight: 400;
            color: $color-black-3;
            display: flex;
            align-items: flex-start;
            padding-top: 10px;
            line-height: 1.4;
