.news-list {
    .filter-list-item:not(:first-child) {
        font-family: $font-primary;
    }
    &__row {
        display: flex;
        flex-wrap: wrap;
        // gap: 65px 9px;
        // margin-bottom: 65px;
        position: relative;
        margin: 0 -5px 20px;
        // &:nth-last-child(2){
        //     margin-bottom: 0;
        //     &::after{
        //         display: none;
        //     }
        // }

        @include mq("tablet") {
            flex-direction: row;
            margin: 0 -5px 0;
            // gap: 11px;
            // margin-bottom: 83px;

            &::after {
                content: "";
                position: absolute;
                background: $color-off-white;
                width: 100%;
                height: 10px;
                bottom: 40px;
                left: 0;
            }
        }
    }

    &__col {
        flex: 0 0 calc(100% / 2);
        padding: 0 5px;
        margin-bottom: 40px;
        @include mq("tablet") {
            flex: 0 0 calc(100% / 4);
            border-bottom: solid 1px $color-bdr;
            padding: 0 5px 40px;
        }
    }

    &__img {
        overflow: hidden;
        img {
            width: 100%;
            display: block;
            transition: all 0.25s ease;
        }
    }

    &__date {
        line-height: calc(19 / 14);
        margin-block: 11px 10px;
        flex-wrap: wrap;

        span {
            font-weight: 700;
            color: $color-primary;
            display: block;
            @include mq("tablet") {
                // padding-left: 12px;
                display: inline-block;
            }
            &:first-child {
                padding-right: 12px;
                font-size: 1.4rem;
                color: $color-black;
                font-weight: 400;
            }
        }
    }

    &__para {
        color: $color-black-2;
        font-size: 1.6rem;
        line-height: calc(22 / 16);
        font-weight: 500;
        @include line-clamp(3);
    }
    &__lnk {
        transition: all 0.25s ease;
        &:hover {
            .news-list__img {
                img {
                    transform: scale(1.1);
                }
            }
        }
    }
}
